                    <span data-component-id="src/components/common/AssignTaskModal.tsx:210:20" data-component-path="src/components/common/AssignTaskModal.tsx" data-component-line="210" data-component-end-line="210" data-component-file="AssignTaskModal.tsx" data-component-name="span" data-component-content="%7B%22elementName%22%3A%22span%22%2C%22textContent%22%3A%22Assigning...%22%7D">Assigning...</span>
