    const downtime = parseFloat(downtimeCost) || 0;
    const maintenance = parseFloat(maintenanceCost) || 0;
    const reduction = parseFloat(downtimeReduction) || 0;

    const savings = (downtime * (reduction / 100)) * 12;
    const investment = maintenance * 12;
    const roi = investment > 0 ? ((savings - investment) / investment) * 100 : 0;
    const paybackMonths = investment > 0 ? investment / (savings / 12) : 0;

